<template>
  <div>
    <h1>添加学生</h1>
    <add-or-edit @submit="handleSubmit"></add-or-edit>
  </div>
</template>

<script>
import AddOrEdit from "./components/AddOrEdit";
export default {
  components: {
    AddOrEdit
  },
  methods: {
    async handleSubmit(stu) {
      await fetch("/api/student", {
        method: "post",
        body: JSON.stringify(stu),
        headers: {
          "content-type": "application/json"
        }
      });
      this.$message({
        message: "添加学生成功",
        type: "success"
      });
    }
  }
};
</script>